
import {
  IonContent,
  IonFab,
  IonFabButton,
  IonHeader,
  IonItem,
  IonList,
  IonPage,
  IonSearchbar,
  IonTitle,
  IonToolbar,
} from "@ionic/vue";
import { defineComponent, ref } from "vue";

export default defineComponent({
  components: {
    IonContent,
    IonFab,
    IonFabButton,
    IonHeader,
    IonList,
    IonItem,
    IonPage,
    IonSearchbar,
    IonTitle,
    IonToolbar,
  },
  props: {
    content: {
      type: Object as () => Record<string, any>[],
      required: true,
    },
    contentKey: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      required: true,
    },
    modalCallback: {
      type: Function,
      required: true,
    },
  },
  setup(props) {
    const searchQuery = ref("");
    const searchResults = () => {
      if (searchQuery.value) {
        console.log(props.contentKey);
        return props.content.filter((item) => {
          if (props.contentKey) {
            return (
              item[props.contentKey]
                .toLowerCase()
                .indexOf(searchQuery.value.toLowerCase()) != -1
            );
          } else {
            console.log(item);
            return (
              item.toLowerCase().indexOf(searchQuery.value.toLowerCase()) != -1
            );
          }
        });
      } else {
        return props.content;
      }
    };
    const getValue = (item: Record<string, any>) => {
      if (props.contentKey) {
        return item[props.contentKey];
      } else {
        return item;
      }
    };
    return {
      getValue,
      searchQuery,
      searchResults,
    };
  },
});
